import { useEffect } from 'react';
// material
import { Container, Grid, Alert } from '@mui/material';
// hooks
import useAuth from '../../hooks/useAuth';
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import { PATH_DASHBOARD } from '../../routes/paths';
import {
  AppTotalPerte,
  AppTotalFichesMontant,
  AppTotalCommission,
  AppTotalGain,
  AgentRap,
  SupRap,
  TotalRap,
  AppTotalAnother
} from '../../components/_dashboard/general-app';
import { useDispatch, useSelector } from '../../redux/store';
import { getAppJournalier, getAgentRapports, getSupRapports, getASRapports } from '../../redux/slices/user';
import { handlePermission } from '../../utils/handlePermission';
import { PERMISSIONS } from '../../guards/Permissions';
// ----------------------------------------------------------------------

export default function GeneralApp() {
  const { themeStretch } = useSettings();
  const { user } = useAuth();

  const dispatch = useDispatch();
  const { appJournalier, agentRapport, supRapport, agentsupRapport } = useSelector((state) => state.user);

  useEffect(() => {
    dispatch(getAppJournalier());
    dispatch(getAgentRapports());
    dispatch(getSupRapports());
    dispatch(getASRapports());
  }, [dispatch]);

  return (
    <Page title="General: App | RLP Center Lotto">
      <Container maxWidth={themeStretch ? false : 'xl'}>
        <Grid container spacing={3}>
          {/* <Grid item xs={12} md={12}>
            <Alert variant="filled" severity="info">
              Verifier lot gagnant w avan w f kontrol, sil pa ko moute ou k toujou metel !
            </Alert>
          </Grid> */}
          {user?.status_payment === 'expired' && (
            <Grid item xs={12} md={12}>
              <Alert variant="filled" severity="error">
                {user?.status_payment !== 'expired'
                  ? `Compte expira dans ${user?.status_payment} jours`
                  : 'Compte expiré'}
              </Alert>
            </Grid>
          )}

          {user?.status_payment <= 10 && (
            <Grid item xs={12} md={12}>
              <Alert variant="filled" severity="error">
                {user?.status_payment !== 'expired'
                  ? `Compte expira dans ${user?.status_payment} jours`
                  : 'Compte expiré'}
              </Alert>
            </Grid>
          )}

          {handlePermission(user?.perms, PERMISSIONS.VIEW_REPORT_PARTIEL) && user?.role !== 'superuser' && (
            <>
              <Grid item xs={12} md={3}>
                <AppTotalFichesMontant
                  ficheMontant={appJournalier?.ficheMontant}
                  ficheCount={appJournalier?.ficheCount}
                />
              </Grid>
              <Grid item xs={12} md={3}>
                <AppTotalPerte fichePerteMontant={appJournalier?.fichePerteMontant} />
              </Grid>
              <Grid item xs={12} md={3}>
                <AppTotalCommission commission={appJournalier?.commission} />
              </Grid>
              <Grid item xs={12} md={3}>
                <AppTotalGain ficheGainMontant={appJournalier?.ficheGainMontant} />
              </Grid>
            </>
          )}

          {handlePermission(user?.perms, PERMISSIONS.VIEW_REPORT_PARTIEL) && user?.role === 'superuser' && (
            <>
              <Grid item xs={12} md={2.4}>
                <AppTotalAnother
                  fontSize={14}
                  text="Total Fiches Generales"
                  bgColor="red"
                  value={appJournalier?.ficheCount}
                />
              </Grid>
              <Grid item xs={12} md={2.4}>
                <AppTotalAnother
                  fontSize={14}
                  text="Total Fiches Gagnants"
                  bgColor="#FF9000"
                  value={appJournalier?.ficheWinCount}
                />
              </Grid>
              <Grid item xs={12} md={2.4}>
                <AppTotalAnother
                  fontSize={14}
                  type="float"
                  text="Total Ventes"
                  bgColor="#6F1AB6"
                  value={appJournalier?.ficheMontant}
                />
              </Grid>
              <Grid item xs={12} md={2.4}>
                <AppTotalAnother
                  fontSize={14}
                  type="float"
                  text="Total Commission"
                  bgColor="#B31313"
                  value={appJournalier?.commission}
                />
              </Grid>
              <Grid item xs={12} md={2.4}>
                <AppTotalAnother
                  fontSize={14}
                  type="float"
                  text="Total montant a payer"
                  bgColor="#5D6EC7"
                  value={appJournalier?.fichePerteMontant}
                />
              </Grid>
              <Grid item xs={12} md={2.4}>
                <AppTotalAnother
                  fontSize={14}
                  type="float"
                  text="Total Balance"
                  bgColor="#2192FF"
                  value={appJournalier?.ficheGainMontant}
                />
              </Grid>
              <Grid item xs={12} md={2.4}>
                <AppTotalAnother
                  fontSize={14}
                  text="Total Compagnies"
                  bgColor="#6C00FF"
                  value={appJournalier?.companyCount}
                />
              </Grid>
              <Grid item xs={12} md={2.4}>
                <AppTotalAnother
                  fontSize={14}
                  text="Total Agent prepayées"
                  bgColor="#226597"
                  value={appJournalier?.agentPrepaidCount}
                />
              </Grid>
              <Grid item xs={12} md={2.4}>
                <AppTotalAnother
                  fontSize={14}
                  text="Pos Actifs"
                  bgColor="#128061"
                  value={appJournalier?.agentActifCount}
                />
              </Grid>
              <Grid item xs={12} md={2.4}>
                <AppTotalAnother
                  fontSize={14}
                  text="Pos Inactifs"
                  bgColor="#F95959"
                  value={appJournalier?.agentInactifCount}
                />
              </Grid>
            </>
          )}

          <Grid item xs={12} md={12}>
            <AgentRap list={agentRapport} user={user} />
          </Grid>
          {supRapport?.length > 0 && (
            <Grid item xs={12} md={12}>
              <SupRap list={supRapport} user={user} />
            </Grid>
          )}

          <Grid item xs={12} md={12}>
            <TotalRap rapport={agentsupRapport} />
          </Grid>
        </Grid>
      </Container>
    </Page>
  );
}
